<template>
  <v-container fluid class="ma-8">
    <v-row>
      <v-col cols="12" md="4" center>
        <v-row>
          <v-col cols="12" class="d-flex justify-center">
            <v-hover>
              <template v-slot:default="{ hover }">
                <v-avatar size="180" class="avatar-border">
                  <img :src="currentUser.avatar" v-if="currentUser.avatar" />

                  <span class="text-h1" v-else>
                    <!-- 没有头像时,在头像框显示用户名的第一个字 -->
                    {{ currentUser.username[0].toUpperCase() }}
                  </span>
                  <v-fade-transition>
                    <v-overlay v-if="hover" absolute>
                      <v-btn
                        height="180"
                        width="180"
                        large
                        text
                        fab
                        @click.stop="avatarDialog = true"
                      >
                        更换头像
                      </v-btn>
                    </v-overlay>
                  </v-fade-transition>
                </v-avatar>
              </template>
            </v-hover>
          </v-col>
          <v-col cols="12" class="d-flex justify-center">
            <span class="text-h6">
              {{ currentUser.username }}
            </span>
          </v-col>
        </v-row>
      </v-col>
      <v-col cols="12" md="8">
        <v-row>
          <v-col>
            <span class="text-h5">帐号信息</span>
            <v-divider />
          </v-col>
          <v-col cols="12">
            手机号码<span
              :class="
                currentUser.mobilePhoneVerified ? 'green--text' : 'red--text'
              "
            >
              ({{ currentUser.mobilePhoneVerified | verified }})
            </span>
          </v-col>
          <v-col sm="6" md="4">
            <v-text-field
              single-line
              outlined
              counter
              disabled
              dense
              :value="currentUser.mobilePhoneNumber"
            >
            </v-text-field>
          </v-col>
          <v-col cols="12" sm="6" md="4">
            <v-dialog v-model="phoneNumberDialog" width="500">
              <template v-slot:activator="{ on, attrs }">
                <v-btn v-bind="attrs" v-on="on" class="mr-3"> 更换号码 </v-btn>
              </template>
              <v-card>
                <v-card-title class="headline grey lighten-2" primary-title>
                  更换号码
                </v-card-title>
                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12">
                        <v-text-field label="新手机号码"></v-text-field>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn
                    color="primary"
                    text
                    @click="phoneNumberDialog = false"
                  >
                    取消
                  </v-btn>
                  <v-btn
                    color="success"
                    text
                    @click="phoneNumberDialog = false"
                  >
                    保存
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <v-btn v-if="!currentUser.mobilePhoneVerified">验证</v-btn>
          </v-col>
          <v-col cols="12">
            邮箱
            <span
              :class="currentUser.emailVerified ? 'green--text' : 'red--text'"
            >
              ({{ currentUser.emailVerified | verified }})
            </span>
          </v-col>
          <v-col sm="6" md="4">
            <v-text-field
              single-line
              outlined
              counter
              disabled
              dense
              :value="currentUser.email"
            >
            </v-text-field>
          </v-col>
          <v-col sm="6" md="4">
            <v-btn v-if="!currentUser.emailVerified">验证</v-btn>
          </v-col>
          <v-col cols="12">
            <span>登录密码</span>
          </v-col>
          <v-col sm="6" md="4">
            <v-text-field single-line outlined disabled dense value="********">
            </v-text-field>
          </v-col>
          <v-col sm="6" md="4">
            <v-dialog v-model="passwordDialog" persistent max-width="600px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn v-bind="attrs" v-on="on"> 修改密码 </v-btn>
              </template>
              <v-card>
                <v-card-title>
                  <span class="headline">修改密码</span>
                </v-card-title>
                <v-divider />
                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12">
                        <v-text-field
                          label="当前密码*"
                          type="password"
                          required
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12">
                        <v-text-field
                          label="新密码*"
                          type="password"
                          required
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12">
                        <v-text-field
                          label="确认密码*"
                          type="password"
                          required
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </v-container>
                  <small class="red--text">* 表示必填字段</small>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn
                    color="blue darken-1"
                    text
                    @click="passwordDialog = false"
                    >Close</v-btn
                  >
                  <v-btn color="success" text @click="passwordDialog = false"
                    >Save</v-btn
                  >
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-col>
          <v-col cols="12">
            <span>用户昵称</span>
          </v-col>
          <v-col sm="6" md="4">
            <v-text-field
              single-line
              outlined
              counter
              disabled
              dense
              :value="currentUser.nickname"
            >
            </v-text-field>
          </v-col>
          <v-col sm="6" md="4">
            <v-btn>修改昵称</v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>

    <v-dialog v-model="avatarDialog" hide-overlay width="600">
      <v-card>
        <v-card-title class="headline">
          <span>更换头像</span>
          <v-spacer />
          <v-btn icon @click="avatarDialog = false">
            <v-icon color="red">mdi-close-thick</v-icon>
          </v-btn>
        </v-card-title>

        <v-divider />
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="8">
                <div
                  class="place"
                  @click="updateImage"
                  v-if="image.length === 0"
                >
                  <div class="place-circle"></div>
                </div>
                <v-img
                  :src="image"
                  height="300"
                  contain
                  border="2"
                  v-else
                ></v-img>
              </v-col>
              <v-col cols="4">
                <p class="text-center">预览</p>
                <v-avatar size="160" class="avatar-border">
                  <img
                    :src="
                      image.length === 0
                        ? $store.state.currentUser.avatar
                        : image
                    "
                  />
                </v-avatar>
                <p class="text-center">160 x 160</p>
              </v-col>
            </v-row>
          </v-container>
          <v-overlay :value="updateOverlay">
            <v-progress-circular indeterminate size="64"></v-progress-circular>
          </v-overlay>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <input
            accept="image/*"
            type="file"
            ref="selectFile"
            hidden
            @change="loadImageFile"
          />
          <v-btn color="green darken-1" text @click="updateImage"> 上传 </v-btn>

          <v-btn
            color="orange darken-4 white--text"
            @click="saveAvatar"
            :disabled="imageFile === ''"
          >
            保存
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import AccountService from "@/service/account_service";
export default {
  name: "user-info",
  data: () => ({
    // 当前用户, {} 内的值只是用于还没有读取数据之前进行页面设计时使用
    currentUser: {
      username: "admin",
      email: "abc@163.com",
      emailVerified: false,
      mobilePhoneNumber: "1373737910640",
      mobilePhoneVerified: true,
      password: "******",
      nickname: "Admin",
      avatar: require("@/assets/avatar.png"), //使用 require 导入静态图片
    },
    
    avatarDialog: false, // 显示更换头像的对话框
    phoneNumberDialog: false, // 显示更换电话号码的对话框
    passwordDialog: false, // 显示修改密码的对话框
    phoneNumber: "",
    password: "********",
    reenterpassword: "",
    image: [], // 用于在对话框中显示本地选择的图片
    imageFile: "", // 用于上传到服务器的图片对象
    updateOverlay: false, // 显示上传图片的进度圆圈
  }),
  mounted() {
    this.getCurrentUser();
  },
  methods: {
    getCurrentUser() {
      this.currentUser = AccountService.currentUser();
    },

    // 打开上传图片对话框
    updateImage() {
      // 把真正的图片上传表单控件 隐藏起来，用按钮来触发其单击事件
      // selectFile 为 input 的属性  ref="selectFile" 对应的值
      this.$refs.selectFile.click();
    },

    // 读取并显示本地图片
    loadImageFile() {
      console.log("load image...");
      // selectFile 为 input 的属性  ref="selectFile" 对应的值
      this.imageFile = this.$refs.selectFile.files[0];

      // 获取所选图片的本地url
      this.image = window.URL.createObjectURL(this.imageFile);
    },

    // 保存用户的头像
    saveAvatar() {
      this.updateOverlay = true;
      if (this.currentUser.id && this.imageFile !== "") {
        console.log(this.imageFile);
        AccountService.updateAvatar(this.imageFile)
          .then((user) => {
            //
            this.currentUser = user;
            // 更新用户信息到 vuex 状态中
            this.$store.commit("SET_CURRENT_USER", user);
            this.avatarClose();
          })
          .catch((error) => {
            this.avatarClose();
            //
          });
      }
    },

    // 关闭头像对话框时还原一些变量
    avatarClose() {
      this.avatarDialog = false;
      this.updateOverlay = false;
      this.image = [];
      this.imageFile = "";
    },
  },

  watch: {
    // 如果关闭更换头像对话框，
    avatarDialog(val) {
      val || this.avatarClose();
    },
  },
  // 过滤器
  filters: {
    verified: function (val) {
      if (val) {
        return "已验证";
      } else {
        return "未验证";
      }
    },
  },
};
</script>

<style scoped>
.avatar-border {
  border: 2px solid gray;
}

.place {
  width: 300px;
  height: 300px;
  background-color: gray;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.place-circle {
  border-radius: 50%;
  width: 250px;
  height: 250px;
  background-color: white;
}
</style>